.container{display:flex;flex-direction:column;margin-top:50px}.container p{font-family:"Fasthand",cursive}.under{text-decoration:underline}.quote-box{max-width:500px;width:70vw;margin-top:50px;margin:30px auto;padding:30px;color:#000;font-family:Georgia,"Times New Roman",Times,serif;box-shadow:#f4cae4 0px 20px 30px -10px;border-radius:10px;background-image:url("../../images/cloud-phone.jpg");background-repeat:no-repeat;background-size:cover;background-position:center;height:100%;font-size:clamp(12px,2vw,20px)}.quote-box blockquote{border:none;margin-bottom:-30px;quotes:"“" "”" "‘" "’";line-height:1.25}.quote-box blockquote h2{font-size:18px;font-size:clamp(12px,2vw,20px)}.quote-box blockquote h2:before{content:open-quote;font-size:30px}.quote-box figcaption{text-align:right;line-height:4}.social{display:flex}.social img{image-rendering:high-quality;padding:10px;opacity:1}.social button{font-family:Georgia,"Times New Roman",Times,serif;font-weight:bold;margin-left:auto;border:none;border:1px solid #f4cee1;background-color:rgba(0,0,0,0);height:50px;border-radius:10px;box-shadow:rgba(238,227,193,.2) 0px 8px 24px;animation:buttonFloat 2s infinite forwards}.social button:hover{cursor:pointer}@keyframes buttonFloat{25%{transform:rotate(-5deg)}75%{transform:rotate(5deg)}}